body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.botonimagen1{
    background-image: url("/usuario_normal1.png");
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 50px;
    text-shadow: 5px 5px rgb(0, 0, 0);
  }

.botonimagen1:hover{
  font-size: 80px;
  text-shadow: 5px 5px rgb(182, 6, 6);
}

.botonimagen2{
  background-image: url("/usuario_normal.jpg")  !important;
  text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 50px;
    text-shadow: 5px 5px rgb(0, 0, 0);
}

.botonimagen2:hover{
  font-size: 80px;
  text-shadow: 5px 5px rgb(182, 6, 6);
}
